*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
 
  .container .boxe{
    height:100%;
    width: 100%;
    background: #fff;
    border-radius: 12px;
    padding:2rem 0.5rem 0 1rem ;
    margin-top: 20px;
    box-shadow: 0px 0px 10px 1px #b3b3b3;
  }
  .container ul{
    list-style: none;
    display: flex;
    position: relative;
    justify-content: space-between;
    line-height: 40px;
    align-items: center;
    justify-items: center;
  }
  .hell{
   font-weight: 700;
  }
  .btn{
    padding: 5px 10px 5px 10px;
    background: #ff471a;
    color: #fff;
    border: none;
    outline: none;
    border-radius: 15px;
    height: 50px;
    margin-top: 15px;
    cursor: pointer;
    position: relative;
    right: 0px;
    z-index: 100;
    transition: transform 0.5s;
 
  }
  .btn:hover{
    background: #ff3300;
  }
  .compound{
    padding: 0px 20px 20px 20px;
  }
  .detail{
   box-shadow: 0px 0px 10px 1px #b3b3b3;
