.page ul{
  margin: 100px 0 10px 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.left,.right{
  margin-right: 2rem;
  cursor: pointer;
  
}
.left:hover,.right:hover{
  background: #ff5c33;
  
}
.but{
  color:  #333333;
  padding: 10px 10px 10px 10px;
  font-size: 20px;
  font-weight: 800;
  margin-right: 2rem;
  cursor: pointer;
  transition: transform 5s ease-in-out;
 
  }
.but:hover{
  background-color: #ff5c33 ;
  
}
.buts{
  color:  #333333;
  padding: 10px 10px 10px 10px;
  font-size: 20px;
  font-weight: 800;
  margin-right: 2rem;
  cursor: pointer;
  transition: transform 5s ease-in-out;
  background: #ff5c33;
 
  }
